import React, { useState } from 'react';
import './App.css';
import Checkbox from '@mui/material/Checkbox';
import Slider from '@mui/material/Slider';
import Button from '@mui/material/Button';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import AddIcon from '@mui/icons-material/Add';


//Week slider marks
const marks = [
  { value: 1, label: 'Monday' },
  { value: 2, label: 'Tuesday' },
  { value: 3, label: 'Wednesday' },
  { value: 4, label: 'Thursday' },
  { value: 5, label: 'Friday' },
  { value: 6, label: 'Saturday' },
  { value: 7, label: 'Sunday' },
];

//Day slider marks
const HourMarks = [
  { value: 6, label: '6' },
  { value: 12, label: '12' },
  { value: 18, label: '18' },
  { value: 24, label: '24' },
];

//Week slider configuration
function MySlider({ week, value, handleChange, disabled }) {
  return (
    <Slider
      value={value}
      onChange={(event, newValue) => handleChange(week, newValue)}
      step={1}
      marks={marks}
      valueLabelDisplay="auto"
      min={1}
      max={7}
      disabled={disabled}
    />
  );
}

//Day slider configuration
function MyHourSlider({ day, week, value, handleHourChange }) {
  return (
    <Slider
      value={value}
      onChange={(event, newValue) => handleHourChange(day, week, newValue)}
      step={1}
      marks={HourMarks}
      valueLabelDisplay="auto"
      min={6}
      max={24}
    />
  );
}

//Calculate current week
function getDateWeek(date) {
  const currentDate = typeof date === 'object' ? date : new Date();
  const januaryFirst = new Date(currentDate.getFullYear(), 0, 1);
  const daysToNextMonday = januaryFirst.getDay() === 1 ? 0 : (7 - januaryFirst.getDay()) % 7;
  const nextMonday = new Date(currentDate.getFullYear(), 0, januaryFirst.getDate() + daysToNextMonday);
  return currentDate < nextMonday
    ? 52
    : currentDate > nextMonday
    ? Math.ceil((currentDate - nextMonday) / (24 * 3600 * 1000) / 7)
    : 1;
}
const weekNumber = getDateWeek();


function App() {
  //Making arrays for storing states
  const initialWeeks = Array.from({ length: 7 }, (_, i) => ({
    id: weekNumber + i,
    days: Array(7).fill(false)
  }));

  const initialDays = Array.from({ length: 7 }, (_, i) => ({
    id: weekNumber + i,
    Hours: Array(7).fill([8, 16])
  }));

  //useState hooks
  const [weeks, setWeeks] = useState(initialWeeks);
  const [sliderValues, setSliderValues] = useState(
    Object.fromEntries(initialWeeks.map(week => [week.id, [1, 2]]))
  );
  const [HourSlider, setHourSlider] = useState(
    Object.fromEntries(initialDays.map(day => [day.id, Array(7).fill([8, 16])]))
  );
  const [checkboxValues, setCheckboxValues] = useState(
    Object.fromEntries(initialWeeks.map(week => [week.id, true]))
  );
  const [ShowDays, setShowDays] = useState(
    Object.fromEntries(initialWeeks.map(week => [week.id, false]))
  );

  //setting the selected days visible, and unselected invisible. When closing day-view set all days invisible.
  const toggleDayVisibility = (weekId) => {
    const sliderRanges = sliderValues[weekId];
    const selectedDays = new Set();

    for (let i = 0; i < sliderRanges.length; i += 2) {
      if (sliderRanges[i + 1] !== undefined) {
        for (let day = sliderRanges[i]; day <= sliderRanges[i + 1]; day++) {
          selectedDays.add(day);
        }
      }
    }

    if (ShowDays[weekId] === false) {
      setShowDays(prevValues => ({
        ...prevValues,
        [weekId]: true,
      }));
      selectedDays.forEach(day => {
        setWeeks(prevWeeks =>
          prevWeeks.map(week =>
            week.id === weekId
              ? { ...week, days: week.days.map((d, index) => (index === day - 1 ? true : d)) }
              : week
          )
        );
      });
    } else {
      setShowDays(prevValues => ({
        ...prevValues,
        [weekId]: false,
      }));
      setWeeks(prevWeeks =>
        prevWeeks.map(week =>
          week.id === weekId
            ? { ...week, days: week.days.map(() => false) }
            : week
        )
      );
    }
  };
  //handling actions and changing states
  const handleChange = (week, newValue) => {
    setSliderValues(prevValues => ({
      ...prevValues,
      [week]: newValue,
    }));
  };

  const handleHourChange = (day, week, newValue) => {
    setHourSlider(prevValues => ({
      ...prevValues,
      [week]: prevValues[week].map((hours, index) => (index === day - 1 ? newValue : hours))
    }));
  };

  const handleCheckboxChange = (week, event) => {
    setCheckboxValues(prevValues => ({
      ...prevValues,
      [week]: event.target.checked,
    }));
  };

  const addSliderPoints = (weekId) => {
    setSliderValues(prevValues => {
      const currentValues = prevValues[weekId];
      if (currentValues.length === 4) {
        // If there are already 4 points, remove the additional points
        return {
          ...prevValues,
          [weekId]: [currentValues[0], currentValues[1]],
        };
      }
      // Otherwise, add two more points
      return {
        ...prevValues,
        [weekId]: [...currentValues, 3, 4],
      };
    });
  };

  return (
    <div className="App">
      <div className="Container">
        <h3 className="H3text">MY AVAILABILITY FOR THE NEXT 7 WEEKS</h3>
        {weeks.map(({ id: week }) => (
          <div className="WeekContainer" key={week}>
            <div>
              <p className="WeekNumbers">{week}</p>
              <Checkbox defaultChecked onChange={event => handleCheckboxChange(week, event)} />
            </div>
            <div className="SliderContainer">
              <MySlider
                week={week}
                value={sliderValues[week]}
                handleChange={handleChange}
                disabled={!checkboxValues[week]}
              />
              {checkboxValues[week] && (
                <Button variant="outlined" onClick={() => toggleDayVisibility(week)}>
                  {!ShowDays[week] ? <ArrowDownwardIcon /> : <ArrowUpwardIcon />}
                </Button>
              )}
              {marks.map(({ value, label }) => (
                <div
                  key={value}
                  style={{ display: weeks.find(w => w.id === week).days[value - 1] ? 'block' : 'none' }}
                >
                  {label}
                  <MyHourSlider
                    week={week}
                    day={value}
                    value={HourSlider[week][value - 1]}
                    handleHourChange={handleHourChange}
                  />
                </div>
              ))}
            </div>
            <div>
              <Button onClick={() => addSliderPoints(week)}>
                <AddIcon />
              </Button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default App;
